body{height:100%;width:100%;margin:0;background-image:linear-gradient(90deg, rgba(73,188,222,0.878431) 0, rgba(70,161,253,0.858824) 55%);font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:'Montserrat', sans-serif}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.content{width:100%;height:93vh;background-image:linear-gradient(90deg, rgba(73,188,222,0.878431) 0, rgba(70,203,253,0.858824) 55%);background-repeat:no-repeat;background-size:cover;display:flex;justify-content:center;align-items:center}

.topbar{height:60px;display:flex;padding:10px;align-items:center;justify-content:space-between}.user-icon{font-size:24px}.powereye-logo{width:150px}.ant-layout-header{background:#ffffff;border-bottom:solid 0.5px}

.home{display:flex;flex-direction:column;width:80%}.home .button-group{display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap}.home .button-group .ant-row{width:100%}.title-card{width:100%;border-radius:8px;box-shadow:-2px 2px 6px #7b7b7b;text-align:center}

.buttom-card{border-radius:8px;margin-right:10px;margin-left:10px;box-shadow:-2px 2px 6px #7b7b7b}.buttom-card img{width:100%;height:auto;max-width:330px}.buttom-card .ant-card-body{display:flex;flex-direction:column;align-items:center}


.indicator-group{text-align:center;margin-top:20px}.indicator-group .indicator-row{margin:25px;display:flex;flex-wrap:wrap;justify-content:center}

.indicator-card{border-radius:6px;margin:15px}.indicator-card:hover{box-shadow:0 1px 2px -2px rgba(0,0,0,0.16),0 3px 6px 0 rgba(0,0,0,0.12),0 5px 12px 4px rgba(0,0,0,0.09)}.indicator-card .stat-number{font-size:20px}.indicator-card .stat{text-align:center}

.chart{margin:auto;max-width:1200px}

.chart-group{text-align:center}.cake-chart-card{width:50%}.filter-chart-group{display:flex;flex-direction:row;justify-content:space-evenly;margin:20px auto;max-width:400px}

.login-page{width:100%;height:100%;position:absolute;top:0;left:0;background-image:linear-gradient(100deg, rgba(73,151,222,0.878431), rgba(70,237,253,0.509804) 55%);background-repeat:repeat-x;background-size:contain;display:flex;align-items:center;justify-content:center;flex-flow:column}.form-box{width:80%;max-width:550px;border-radius:8px;padding:10px}.only-logo{margin-bottom:20px}


.config-navigator{margin-top:20px;display:flex;align-items:center;justify-content:space-evenly;flex-wrap:wrap}.config-navigator button{margin:5px}

.showcase{margin:auto;width:85%}body{background-image:none;background-color:#eff2f5}.sector-content{display:flex;justify-content:center;align-items:center;background-image:url(/static/media/glass.3d7c39c5.png);background-repeat:no-repeat;background-size:cover;border:none}.fixed-index{position:absolute;top:10px;left:15px}.text-option{padding:7px;border-radius:10px;color:darkcyan;font-size:26px;border:1px solid}.sync-state{display:flex;flex-direction:column;align-items:center}.sync-state .spin-icon{font-size:20px}.sync-state .success-icon{font-size:32px;color:#05b305;font-weight:800}

.ant-list{background-color:white;min-width:300px}.ant-list-item{justify-content:flex-start}

.priority-content{display:flex;justify-content:space-evenly;flex-wrap:wrap}

